























































































































.real-time-data {
  position: relative;
  width: 100%;
  height: 100%;
}

.map-container {
  width: 100%;
  min-height: 100vh;
  background-color: #f0f0f0;
}

.info-panel {
  position: absolute;
  width: 300px;
  background-color: rgba(255, 255, 255, 0.9);
  border-radius: 8px;
  padding: 16px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.2);
}

.top-panel {
  top: 16px;
  left: 50%;
  transform: translateX(-50%);
}

.bottom-panel {
  bottom: 16px;
  left: 50%;
  transform: translateX(-50%);
}

.left-panel {
  top: 50%;
  left: 16px;
  transform: translateY(-50%);
}

.right-panel {
  top: 50%;
  right: 16px;
  transform: translateY(-50%);
}

.data-list {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.data-item {
  display: flex;
  justify-content: space-between;
  font-size: 14px;
  color: #666;
}

.data-label {
  font-weight: bold;
  color: #333;
}

.data-value {
  color: #007bff;
}
